import React from 'react';
import { AppBar, Button, Toolbar, Hidden, IconButton, Drawer, List, ListItem } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import MenuIcon from '@material-ui/icons/Menu';
import navbg from '../Extras/navbg.png';
import katlogo from '../Extras/katlogo.png';
import logoName from '../Extras/logoName.png';
import logoNamehov from '../Extras/logoNamehov.png';

import { NavLink } from 'react-router-dom';

import AboutMe from './SubMenu/AboutMe';
import Home from './SubMenu/Home';
import ExperienceSkills from './SubMenu/ExperienceSkills';
import Projects from './SubMenu/Projects';
import Resume from './SubMenu/Resume';

const useStyles = makeStyles((theme) => ({
    headerBackground: {
        display: 'inline-flex',
        backgroundColor: '#fff',
        backgroundImage: `url(${navbg})`,
        backgroundSize: '130px 130px',
        height: '130px',
        borderBottom:  '5px solid',
        color: theme.palette.primary.main,
        padding: '0 2em',
        justifyContent: 'center',
        [theme.breakpoints.down('sm')]:{
            padding: '0',
            height: 'auto',
        },
    },
    logo:{
        alignItems: 'center',
        display: 'inline-flex',
    },
    logoImg: {
        width: '130px',
        padding: theme.spacing(2),
    },
    logoName: {
        [theme.breakpoints.down('sm')]:{
            width: '100%',
        }
    },
    logoNameImg: {
        padding: theme.spacing(3),
        position: 'relative',

    },
    logoNameHov: {
        position: 'absolute',
        opacity: 0,
        transition: 'opacity 0.2s ease-out',
        top: theme.spacing(3),
        left: theme.spacing(3),
        '&:hover': {
            opacity: 1,
        },
    },
    tabsStyle: {
        display: 'inline-flex',
        textcolor: theme.palette.primary.main,
        alignItems: 'center',
        [theme.breakpoints.down('sm')]:{
            display: 'block',
        }
    },
    headerAlign: {
        display: 'inline-flex',
        flex: 5,
    },
    tabVisited: {
        textDecoration: 'none !important',
        color: theme.palette.primary.main,
        '&:hover': {
            backgroundColor: 'rgba(0,0,0,0)',
            color: `${theme.palette.secondary.main} !important`,
            transition: 'ease-in-out 0.3s',
            textShadow: `0 0 4px ${theme.palette.accent.main}`,
        },
    },
    tabSelected: {
        textDecoration: 'none !important',
        color:`${theme.palette.secondary.main} !important`,
        opacity: 1,
    },
    tab: {
        opacity: 0.8,
        padding: theme.spacing(2),
        transition: 'ease-in-out 0.2s',
        '&:hover': {
            backgroundColor: 'rgba(0,0,0,0)',
            color: `${theme.palette.secondary.main} !important`,
            transition: 'ease-in-out 0.3s',
            textShadow: `0 0 4px ${theme.palette.accent.main}`,
            cursor: 'pointer',
        },
    },
}));

const Main = (props) => {
    const { window } = props;

    const classes = useStyles();
    const [mobileOpen, setMobileOpen] = React.useState(false);
    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const container = window !== undefined ? () => window().document.body : undefined;

    const logoNameHeader = (
        <div className={classes.logoNameImg}>
            <img src={logoName} className={classes.logoName} alt='Kathryn Ng'/>
            <img src={logoNamehov} className={classes.logoNameHov} alt=''/>
        </div>
    );

    const logoIconHeader = (
        <img src={katlogo} className={classes.logoImg} alt='logo'/>
    );

    const drawer = (
        <List component="ul" aria-label="main navigation area" className={classes.tabsStyle} onClick={() => {handleDrawerToggle()}}>
            <Hidden mdUp>
                <ListItem>
                {logoIconHeader}
                </ListItem>
                <ListItem>
                    <Button disableRipple color='primary' className={classes.tab}>
                        <NavLink exact to='/' activeClassName={classes.tabSelected} className={classes.tabVisited}>
                            home
                        </NavLink>
                    </Button>
                </ListItem>
            </Hidden>
            <ListItem>
                <Button disableRipple color='primary' className={classes.tab}>
                    <NavLink exact to='/about' activeClassName={classes.tabSelected} className={classes.tabVisited}>
                            about
                    </NavLink>
                </Button>                   
            </ListItem>
            <ListItem>
                <Button disableRipple color='primary' className={classes.tab}>
                    <NavLink exact to='/experienceskills' activeClassName={classes.tabSelected} className={classes.tabVisited}>
                        experience + skills
                    </NavLink>
                </Button>
            </ListItem>
            <ListItem>
                <Button disableRipple color='primary' className={classes.tab}>
                    <NavLink exact to='/projects' activeClassName={classes.tabSelected} className={classes.tabVisited}>
                        projects
                    </NavLink>
                </Button>
            </ListItem>
            <ListItem>
                <Button disableRipple color='primary' className={classes.tab}>
                    <NavLink exact to='/resume' activeClassName={classes.tabSelected} className={classes.tabVisited}>
                        résumé
                    </NavLink>
                </Button>
            </ListItem>
        </List>
    )

    return (
        <AppBar className={classes.headerBackground} position='fixed'>
            <Toolbar>
            <Hidden mdUp>
                <IconButton
                    color="primary"
                    aria-label="open drawer"
                    onClick={handleDrawerToggle}
                >
                    <MenuIcon />
                </IconButton>
            </Hidden>
            <div className={classes.headerAlign}>
                <NavLink to='/'>
                    <div className={classes.logo}>
                        <Hidden smDown>
                        {logoIconHeader}
                        </Hidden>
                        {logoNameHeader}
                    </div>
                </NavLink>
            </div>
            <Hidden smDown>
                {drawer}
            </Hidden>
            <Hidden mdUp>
                <Drawer
                    container={container}
                    variant="temporary"
                    anchor={'left'}
                    open={mobileOpen}
                    onClose={handleDrawerToggle}
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                    ModalProps={{
                    keepMounted: true, // Better open performance on mobile.
                    }}
                >
                    {drawer}
                </Drawer>
            </Hidden>
            </Toolbar>
        </AppBar>
    )
};

export default Main;

export { AboutMe, Home, ExperienceSkills, Projects, Resume };