import React from 'react';
import { makeStyles } from '@material-ui/styles';
import AboutMeImg from '../../Extras/AboutMe.png';
import { Typography, Grid, Box, Button } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        [theme.breakpoints.down('md')]: {
            marginBottom: theme.spacing(8),
        }
    },
    heading: {
        color: theme.palette.secondary.main,
        textAlign: 'center',
    },
    img: {
        maxWidth: 'none',
        width: 'auto',
        [theme.breakpoints.down('xs')]: {
            width: '100%',
        }
    },
    about: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    name: {
        display: 'inline-flex',
        alignItems: 'baseline',
        color: theme.palette.primary.main,
        [theme.breakpoints.down('md')]: {
            display: 'flex',
            flexWrap: 'wrap',
        }
    },
    pronouns: {
        margin: '0 0.5em',
        color: theme.palette.secondary.main,
        [theme.breakpoints.down('xs')]: {
            margin: '0',
        },
    },
    contactBtn: {
        color: theme.palette.secondary.main,
        border: '1px solid',
        borderRadius: '5rem',
        borderColor: theme.palette.secondary.main,
        padding: '0.5em 1em',
        '&:hover': {
            color: theme.palette.primary.main,
            borderColor: theme.palette.primary.main,
            backgroundColor: '#F9E8E5',
            boxShadow: "0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12)",
        }
    },
}));

const AboutMe = () => {
    const classes = useStyles();

    return (
            <Grid className={classes.root}>
                <Typography variant='h2' className={classes.heading}>Why, Hello there!</Typography>
                <Typography variant='h1' className={classes.heading}>About me:</Typography>
                <Grid className={classes.about} container>
                    <Grid item md={4} sm={12} >
                        <img src={AboutMeImg} alt='me' className={classes.img}/>
                    </Grid>
                    <Grid item md={5} xs={12}>
                        <Box className={classes.name}>
                            <Typography variant='h1'>
                                Kathryn Ng
                            </Typography>
                            <Typography variant='h2' className={classes.pronouns}>
                                (She/They)
                            </Typography>
                        </Box>
                        <Box m={4}/>
                        <Typography variant='body1'>
                            Kathryn is a 4th year student at the <b>University of British Columbia</b>, Okanagan Campus, and will be graduating in Spring 2022.
                        </Typography>
                        <Box m={1}/>
                        <Typography variant='body1'>
                            Born and raised in Malaysia, and is currently residing in Vancouver, BC. Her interests fall in design and development, and is always wanting to learn how to create improvements in every aspect of life.
                        </Typography>
                        <Box m={1}/>
                        <Typography variant='body1'>
                            To her, education is important, and being able to contribute to build interesting ways for the growing generations to learn is valuable. Most of her projects lean towards fun, hopefully with some kind of practical use. When not grinding her brain at work, she like playing video games and thinking of what to eat for the week.
                        </Typography>
                        <Box m={3}/>
                        <Button href='mailto:kathrynng.design@gmail.com' className={classes.contactBtn}>
                            contact me!
                        </Button>
                        <Box m={1}/>
                        <Typography variant='body1' color='secondary'>
                            or check my other links below:
                        </Typography>
                    </Grid>
                </Grid>
            </Grid>
    )
};
 
export default AboutMe;